.fui-data-picker {
	position: absolute;

	width: 260px;
	padding: 4px;
	border: 1px solid #ddd;
	box-shadow: 0 0 2px #ddd;
	border-radius: 4px;
	table {
		width: 258px;
		margin-left: -4px;
	}
	thead {
		tr {
			height: 30px;
		}
	}
	tbody {
		td:hover {
			cursor: pointer;
			background-color: #eee;
			border-radius: 2px;
		}
	}
	th,td {
		line-height: 21px;
		text-align: center;
		collapse: none;
	}
}
.fui-data-header {

}
.fui-data-select {
	position: relative;
	border: 1px solid #ddd;
	input {
		padding: 0 6px;
		width: 74px;
		height: 24px;
		border: 0;
		outline: none;
	}
	span {
		position: absolute;
		top: 0;
		right: 20px;
	}
	a {
		display: inline-block;
		width: 21px;
		height: 24px;
		line-height: 24px;
		text-align: center;
		background-color: #ddd;
	}
}

.fui-data-table {}

.fui-active {
	border-radius: 2px;
	background-color: #eee;
}

.fui-arrow-right {
	background-image: url("data:image/png;base64,");
	background-size: 60%;
	background-repeat: no-repeat;
	background-position: center center;
}
.fui-arrow-down {
	width: 21px;
	height: 24px;
	background-size: 60%;
	background-repeat: no-repeat;
	background-position: center center;
	background-image: url("data:image/png;base64,");
}
.fui-arrow-left {
	background-size: 60%;
	background-repeat: no-repeat;
	background-position: center center;
	background-image: url("data:image/png;base64,");
}
